@import url(https://fonts.googleapis.com/css2?family=Caveat&display=swap);
.light{
    color: black;
    background-color: white;
    background: white;
}

.dark{
    color: white;
    background-color: black;
    background: black;
    width: 100%;
    height: 100vh;
}

.navbar{padding:10px;margin-bottom:2rem;display:flex;justify-content:space-between;align-items:center;background:#dee9e8;width:1152px;border-radius:25px}.navbar .auths{display:flex}.navbar .auths a{margin-right:15px}.navbar .auths .link{color:#f32295}

.post{display:flex;justify-content:space-between;padding:1.5rem;background:#833ab4;background:linear-gradient(90deg, #833ab4 0%, #fd1d1d 50%, #fcb045 100%)}.post .left{flex:2 1;background:rgba(255,255,255,0.3);padding:10px}.post .left .name,.post .left .desc,.post .left .image{margin-bottom:1rem;display:flex;justify-content:center;align-items:center}.post .left .name{font-size:1.5rem;color:aliceblue}.post .left .image{position:relative;left:52.5px;border-radius:10px;box-shadow:var(--best-shadow)}.post .left .desc{background:white;padding:15px}.post .left .btn-like{margin-bottom:10px;position:relative;left:175px;padding:10px;background:hotpink;color:white;border-radius:10px}.post .left .btn-like:hover{cursor:pointer}.post .left .btn-not-like{margin-bottom:10px;position:relative;left:175px;padding:10px;background:#fff;color:hotpink;border-radius:10px}.post .left .btn-not-like:hover{cursor:pointer}.post .left .desc{font-family:'Caveat', cursive}.post .left .btn-delete,.post .left .btn-edit{padding:0.5rem;margin-right:15px;margin-bottom:10px;margin-left:10px;cursor:pointer;transition:0.5s}.post .left .btn-delete:hover,.post .left .btn-edit:hover{box-shadow:var(--best-shadow)}.post .left .btn-delete{color:red}.post .left .btn-delete:hover{color:white;background:red}.post .left .btn-edit{color:#45a745}.post .left .btn-edit:hover{color:white;background:#45a745}.post .right{flex:3 1;padding:10px;display:flex;flex-direction:column;background:rgba(255,255,255,0.3)}.post .right .comments{margin-bottom:10px;padding:0.5rem;background:rgba(255,255,255,0.15);box-shadow:0 8px 32px 0 rgba(31,38,135,0.37);border-radius:10px;border:1px solid rgba(255,255,255,0.18);height:350px;overflow:hidden}.post .right .comments:hover{overflow:scroll}.post .right .add-comments{flex:150px 1;display:flex;flex-direction:column;padding:10px;background:rgba(255,255,255,0.15);box-shadow:0 8px 32px 0 rgba(31,38,135,0.37);border-radius:10px;border:1px solid rgba(255,255,255,0.18)}.post .right .add-comments textarea{height:113px;margin-top:10px;border-radius:20px;padding:20px;background:rgba(255,255,255,0.15);box-shadow:0 8px 32px 0 rgba(31,38,135,0.37);border-radius:10px;border:1px solid rgba(255,255,255,0.18)}.post .right .add-comments button{margin-top:10px;background:springgreen;width:100px;padding:5px;border-radius:10px;transition:0.5s;z-index:1}.post .right .add-comments button:hover{background:#177423;color:white;cursor:pointer}

.indi-comment{padding:5px;margin-bottom:1px;margin-top:2px;border-radius:10px}.indi-comment .comment-box{background:white;padding:10px;border-radius:10px}.indi-comment .comment-box .comment-by{color:#000;margin-left:10px}.indi-comment .comment-box .comment-by .link{color:red}

.post-editor{padding:1rem;border-radius:10px;background:orange;box-shadow:var(--best-shadow);margin-bottom:10px}.post-editor input,.post-editor textarea{display:block;margin:10px;padding:10px;width:100%}.post-editor textarea{background:wheat;border-radius:10px}.post-editor .btn-upload,.post-editor .btn-cancel{padding:10px;transition:0.5s}.post-editor .btn-upload{color:#2b6340}.post-editor .btn-upload:hover{color:white;background:#2b6340}.post-editor .btn-cancel{color:red}.post-editor .btn-cancel:hover{color:white;background:red}

.home .btn-add-post,.home .btn-my-post{padding:0.5rem;margin-right:15px;margin-bottom:10px;margin-left:10px;cursor:pointer}.home .btn-add-post:hover,.home .btn-my-post:hover{box-shadow:var(--best-shadow)}.home .current-user{display:flex;justify-content:space-between;margin:10px;padding:10px;font-size:21px;background:#e95883;border-radius:10px}

.msg-error{padding:15px;display:flex;justify-content:space-between;background:#f05151;border-radius:5px}.msg-error button:hover{color:#faf2f2;cursor:pointer}

.auth-form{padding:1rem;border-radius:10px;background:orange;box-shadow:var(--best-shadow);width:500px;margin:0 auto}.auth-form .form{display:flex;flex-direction:column;margin:10px;font-size:20px}.auth-form .form input{padding:10px;border:unset;margin-bottom:20px;height:35px;border-radius:12px}.auth-form .form .btn-action{color:green;width:100px;height:30px;transition:0.5s}.auth-form .form .btn-action:hover{color:white;cursor:pointer;background:yellowgreen}.auth-form .form .btn-action .btn-attr{padding:0;font-size:12px}

:root{--primary-color: #eaaef0a9;--light-color: rgb(223, 114, 114);--best-shadow:
    0 2.8px 2.2px rgba(0, 0, 0, 0.034),
    0 6.7px 5.3px rgba(0, 0, 0, 0.048),
    0 12.5px 10px rgba(0, 0, 0, 0.06),
    0 22.3px 17.9px rgba(0, 0, 0, 0.072),
    0 41.8px 33.4px rgba(0, 0, 0, 0.086),
    0 100px 80px rgba(0, 0, 0, 0.12);--text-dark-color: #3a3a3a}*{margin:0;padding:0;box-sizing:border-box}body{background:white;color:#3a3a3a;color:var(--text-dark-color);font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif}.container{border:5px solid #a927ad;margin:0 auto;padding:1.5rem;max-width:1210px;background:#eaaef0a9;background:var(--primary-color);border-radius:25px}button{border:unset;background:unset;font:unset;color:unset}a{color:unset;text-decoration:unset;color:#d9f58d}textarea{color:unset;background:unset;border:unset}

